<template>
  <div>
    <div class="logintitle">
      <img src="../../image/logo.jpg" style="width:100px;height:100px;" alt />
      <!-- <div>商机管理</div> -->
    </div>
    <div style="font-size:20px;color:red;text-align:center;margin-top:10px;" v-if="isSucess">绑定成功!</div>
    <van-form @submit="onSubmit" style="padding:0 30px;" v-else>
      <van-field
        v-model="mobile"
        name="手机号码"
        label="手机号码"
        placeholder="输入手机号码"
        :rules="[{ required: true, message: '请填写手机号码' }]"
      />
      <div style="margin-top: 46px;padding:10px 30px;">
        <van-button round block type="info" native-type="submit">绑定</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Form, Field, Icon, Button, Toast } from "vant";
import Tools from "../../utils/Tools.js";
import axios from "axios";
import qs from "qs";

export default {
  components: {
    [Form.name]: Form,
    [Icon.name]: Icon,
    [Field.name]: Field,
    [Button.name]: Button,
  },
  data() {
    return {
      mobile: "",
      code: "",
      isSucess: false,
    };
  },
  created() {
    if (sessionStorage.getItem("mobile")) {
      this.isSucess = true;
    } else {
      this.code = Tools.getQueryString("code");
      if (!this.code) {
        Tools.initLoginJumpWx();
      }
    }
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    onSubmit(values) {
      let that = this;
      // eslint-disable-next-line no-console
      console.log("submit");
      axios
        .post(
          "http://api.kjics.com/wechat/login/auth",
          qs.stringify({
            code: this.code,
            mobile: this.mobile,
          })
        )
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);
          if (response.data.code === 2000) {
            Toast("绑定成功!");
            that.isSucess = true;
            sessionStorage.setItem("mobile", that.mobile);
          } else {
            Tools.initLoginJumpWx();
            Toast(response.data.msg);
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {
          // eslint-disable-next-line no-mixed-spaces-and-tabs
          Toast("绑定失败");
        });
    },
  },
};
</script>

<style>
body {
  background: white;
}
.logintitle {
  padding-top: 120px;
  padding-bottom: 10px;
  color: #409eff;
  text-align: center;
}
</style>
