//登入获取code
function initwxLogin(loginCallback) {
	//获取微信参数
	var tokenStr = getCurrentToken();

	// eslint-disable-next-line no-console
	console.log("tokenStr==" + tokenStr);
	if (!tokenStr) {
		// eslint-disable-next-line no-console
		console.log("登录===" + tokenStr);
		var loginCode = getQueryString("code");
		// eslint-disable-next-line no-console
		console.log("loginCode", loginCode);
		if (loginCode) {
			var loginUrl = baseUrl() + '/fans/user/fansLogin';
			axios.post(loginUrl, qs.stringify({
				code: loginCode
			})).then(function (data) {
				// eslint-disable-next-line no-console
				console.log(data);
				// eslint-disable-next-line no-redeclare
				var data = data.data;
				if (data.code == 200) {
					if (data.data.token) {
						setCookie("token", data.data.token);
						// setCookie("uid",data.data.uid);
						//window.location.reload();
						loginCallback(data.data.token);
					} else {
						//initLoginJumpWx();
						loginCallback(false);
					}
				} else {
					initLoginJumpWx();
				}
			}).catch(function (data) {
				// eslint-disable-next-line no-console
				console.log(data);
				initLoginJumpWx();
			})
		} else {
			initLoginJumpWx();
		}
	} else {
		// eslint-disable-next-line no-console
		console.log("登录==2222===" + tokenStr);
		loginCallback(tokenStr);
	}
}

