function getQueryString(name) {
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
	var r = window.location.search.substr(1).match(reg);
	if (r != null) return unescape(r[2]);
	return null;
}

// eslint-disable-next-line no-unused-vars
function getQueryString2(urlStr, name) {
	var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
	if (urlStr.indexOf("?") != -1) {
		urlStr = "?" + urlStr.substr(urlStr.indexOf("?") + 1);
		var r = urlStr.substr(1).match(reg);
		if (r != null) return unescape(r[2]);
	}
	return null;
}

function setCookie(name, value, Days) {
	var exp = new Date();
	exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
	document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString() + ';path=/';
}

function getCookie(name) {
	var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
	return (arr = document.cookie.match(reg)) ? unescape(arr[2]) : null;
}

function delCookie(name) {
	setCookie(name, 0, -1);
	// eslint-disable-next-line no-console
	console.log("delCookie");
}

function clearAllCookie() {
	// eslint-disable-next-line no-console
	console.log("clearAllCookie");
	// eslint-disable-next-line no-useless-escape
	var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
	if (keys) {
		for (var i = keys.length; i--;)
			document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()
	}
}

// eslint-disable-next-line no-unused-vars
function changeTime(time) {
	var h = parseInt(time / 3600);
	var m = parseInt(time % 3600 / 60);
	var s = parseInt(time % 60);
	return two(h) + ":" + two(m) + ":" + two(s);
}

function two(num) {
	if (num < 10) {
		return '0' + num;
	}
	return num;
}


function initLoginJumpWx() {
	var redirect_uri = window.location.href;
	// eslint-disable-next-line no-useless-escape
	redirect_uri = redirect_uri.replace(/[&\?]code=.*#/, '#');
	// window.location.href = "http://common.tengface.com/web/wx/loginpage?app_id=qa_system&callback_url="+encodeURIComponent(redirect_uri);
	window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx4efd2a9dbeda83ad&redirect_uri=" + encodeURIComponent(redirect_uri) + "&response_type=code&scope=snsapi_userinfo&state=STATE";
	//http://common.tengface.com/web/wx/loginpage?app_id=qa_system&callback_url=www.baidu.com
}


// eslint-disable-next-line no-unused-vars
function checkPhoneType() {
	var u = navigator.userAgent;
	// 1是安卓机	2 是苹果机	3 未知机型 4 winphone
	if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) { //安卓手机
		return 1;
	} else if (u.indexOf('iPhone') > -1) { //苹果手机
		// window.location.href = "mobile/index.html";
		return 2;
	} else if (u.indexOf('Windows Phone') > -1) { //winphone手机
		return 4;
	} else {
		return 3;
	}
}
